/**
 * mockdown render component
 */
import React from 'react';
import ReactMarkdown from 'react-markdown';
import PropTypes from 'prop-types';

const MockDown = (props) => {
    const { md } = props;
    return <ReactMarkdown>{md}</ReactMarkdown>;
};

MockDown.propTypes = {
    md: PropTypes.string,
};

MockDown.defaultProps = {
    md: '',
};

export default MockDown;
